.App {
  text-align: center;
  width: 100%vw;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

html,body
{
    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
    overflow-x: hidden; 
}

.Header {
  -webkit-animation: Header .8s forwards 0s ease-in;
          animation: Header .8s forwards 0s ease-in;
  color: whitesmoke;
  text-shadow: 1px 1px rgba(143, 153, 247, 0.253);
  font-family: 'Faster One', cursive
}

.poster {
  -webkit-animation: Poster .8s forwards 0s ease-out;
          animation: Poster .8s forwards 0s ease-out;
  -webkit-box-shadow: 6px 6px 5px 2px black;
          box-shadow: 6px 6px 5px 2px black;
  opacity: .8;
  height: 456px;
  width: 300px;
}

.App-intro {
  font-size: large; 
}

.navbar {
  display: inline-block;
}

body {
  background-image: url("../src/movietheaterblur.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.random {
  margin-top: 7vh;
}

.playlist {
  margin-top: 7vh;
}

.playlist-buttons {
  margin: .2em;
}

#directorTable {
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  border-collapse: collapse;
}

#directorTable td, #directorTable th {
  border: 1px solid #ddd;
  padding: 8px;
}

#directorTable tr:nth-child(even){background-color: #f2f2f2;}
#directorTable tr:nth-child(odd){background-color: #b3acac;}

#directorTable tr:hover {background-color: #ddd;}

#directorTable th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: center;
  background-color: black;
  color: whitesmoke;
}

.search {
  margin-top: 7vh;
}

.searchButton {
  border-radius: 10px; 
}

#movie-display {
  color: whitesmoke;
  text-shadow: 2px 2px black;
  font-family: 'Alegreya SC', serif;
  width: 80vw;
  margin-left: 10vw;
  margin-right: 10vw;
}

.movie-display {
  color: whitesmoke;
  text-shadow: 2px 2px black;
  font-family: 'Alegreya SC', serif;
}

.table {
  width: 90wv;
  margin-left: 5vw;
  margin-right: 5vw;
}

h1 {
  font-weight: bold;
  font-size: 32px;
}

p {
  font-size: 21px;
 
}

.movie-info {
  -webkit-animation: fadeIn 3s forwards 0s ease-in;
          animation: fadeIn 3s forwards 0s ease-in;
}

.float-contain {
  padding-left: 18vw;
  padding-right: 18vw;
}

.float {
  float: left;
  margin: 10px;
}

.contain-pie {
  height: 800px;
  width: 100vw;
}

.buttons {
  margin: 5px;
  padding: 8px;
  color: whitesmoke;
}

.buttons:hover {
  color: black;
  background-image: -webkit-radial-gradient(yellow, rgba(197, 197, 31, 0.596), rgba(168, 180, 62, 0.37));
  background-image: -o-radial-gradient(yellow, rgba(197, 197, 31, 0.596), rgba(168, 180, 62, 0.37));
  background-image: radial-gradient(yellow, rgba(197, 197, 31, 0.596), rgba(168, 180, 62, 0.37));
  border-radius: 50%;
  font-weight: bold;
}

.icon {
  font-size: 20px;
  margin: 5px;
  color: whitesmoke;
  text-shadow: 2px 2px black;
}

.hamster {
  border-radius: 50%;
  -webkit-box-shadow: 2px 2px 2px black;
          box-shadow: 2px 2px 2px black;
}

.ReactTable {
  background-color: whitesmoke;
  margin-top: 5vh;
}

.userDisplay {
  margin-top: 5vh;
}

.upcomingDisplay {
  margin-top: 5vh;
  display: grid;
  grid-template-columns: auto auto auto auto;
  grid-gap: .5em;
  color: whitesmoke;
  text-shadow: 2px 2px black;
}

#upcomingTitle {
  font-size: 20px;
}

.upcoming0 {
  grid-area: 1 / 1 / span 1 / span 1;
}

.upcoming1 {
  grid-area: 1 / 2 / span 1 / span 1;
}

.upcoming2 {
  grid-area: 1 / 3 / span 1 / span 1;
}

.upcoming3 {
  grid-area: 1 / 4 / span 1 / span 1;
}

.home:hover ~ #home{
  opacity: 1;
}

.search:hover ~ #search{
  opacity: 1;
}

.userPage:hover ~ #userPage{
  opacity: 1;
}

.upcoming:hover ~ #upcoming{
  opacity: 1;
}

.logout:hover ~ #logout{
  opacity: 1;
}

.login:hover ~ #login{
  opacity: 1;
}

.register:hover ~ #register{
  opacity: 1;
}

.list:hover ~ #list{
  opacity: 1;
}

.vote:hover ~ #vote{
  opacity: 1;
}

.iconText {
  display: block;
  color: whitesmoke;
  margin-top: .5em;
  float: left;
  position: absolute;
  margin-left: 48vw;
  opacity: 0;
}

.voteHeader {
  color: whitesmoke;
  text-shadow: 2px 2px black;
}

.grid {
  display: grid;
  grid-template-columns: auto auto auto auto;
  grid-gap: 1vw;
}

.viewers {
  grid-area: 1 / 1 / span 1 / span 4;
  color: whitesmoke;
  text-shadow: 2px 2px black;
}

.option1 {
  grid-area: 2 / 1 / span 1 / span 1;
  color: whitesmoke;
  text-shadow: 2px 2px black;
}

.option2 {
  grid-area: 2 / 2 / span 1 / span 1;
  color: whitesmoke;
  text-shadow: 2px 2px black;
}

.option3 {
  grid-area: 2 / 3 / span 1 / span 1;
  color: whitesmoke;
  text-shadow: 2px 2px black;
}

.option4 {
  grid-area: 2 / 4 / span 1 / span 1;
  color: whitesmoke;
  text-shadow: 2px 2px black;
}

.optionPoster {
  height: 444px;
  width: 300px;
  -webkit-box-shadow: 6px 6px 5px 2px black;
          box-shadow: 6px 6px 5px 2px black;
  opacity: .8;
}

button:active {
  -webkit-transform: translateY(4px);
      -ms-transform: translateY(4px);
          transform: translateY(4px);
}

.winnerPoster {
  -webkit-animation: WinningMovie 1.5s forwards .2s ease-in;
          animation: WinningMovie 1.5s forwards .2s ease-in;
}

.similarSection {
  font-size: 21px;
}



@media screen and (max-width: 1300px){
  .headerText {font-size: 18px;}
}
@media screen and (max-width: 1200px){
  .headerText {font-size: 16px;}
}
@media screen and (max-width: 1100px){
  .headerText {font-size: 14px;}
}
@media screen and (max-width: 1000px){
  .headerText {font-size: 12px;}
}
@media screen and (max-width: 900px){
  .headerText {font-size: 10px;}
}
@media screen and (max-width: 767px){
  .headerText {font-size: 8px;}
}
@media screen and (max-width: 600px) {
  .headerText{font-size: 6px;}
  .optionPoster {height: 75px; width: 50px;}
  .upcoming0 {grid-area: 1 / 1 / span 1 / span 4}
  .upcoming1 {grid-area: 2 / 1 / span 1 / span 4}
  .upcoming2 {grid-area: 3 / 1 / span 1 / span 4}
  .upcoming3 {grid-area: 4 / 1 / span 1 / span 4}
}

@-webkit-keyframes fadeIn {
  0%{
      opacity: 0;
      }
  33%{
      opacity: 0;
      }
  100%{
      opacity: 1;
      }
}

@keyframes fadeIn {
  0%{
      opacity: 0;
      }
  33%{
      opacity: 0;
      }
  100%{
      opacity: 1;
      }
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@-webkit-keyframes Header{
  0%{
      -webkit-transform: rotate(0) translateY(-150%);
              transform: rotate(0) translateY(-150%);
      opacity: 0;
      }
  37%{
      -webkit-transform: rotate(0) translateY(-150%);
              transform: rotate(0) translateY(-150%);
      }
  100%{
      -webkit-transform: rotate(0) scale(2.5) translateY(20%);
              transform: rotate(0) scale(2.5) translateY(20%);
      opacity: 1;
      }
}

@keyframes Header{
  0%{
      -webkit-transform: rotate(0) translateY(-150%);
              transform: rotate(0) translateY(-150%);
      opacity: 0;
      }
  37%{
      -webkit-transform: rotate(0) translateY(-150%);
              transform: rotate(0) translateY(-150%);
      }
  100%{
      -webkit-transform: rotate(0) scale(2.5) translateY(20%);
              transform: rotate(0) scale(2.5) translateY(20%);
      opacity: 1;
      }
}

@-webkit-keyframes Poster{
  0%{
      -webkit-transform: rotate(0) translateX(-420%) skewX(50deg);
              transform: rotate(0) translateX(-420%) skewX(50deg);
      -webkit-transform-origin: 50% 50%;
              transform-origin: 50% 50%;
      opacity: 0.75;
      -webkit-box-shadow: 6px 6px 5px 2px black;
              box-shadow: 6px 6px 5px 2px black;
      }
  100%{
      -webkit-transform: rotate(0) skewX(0deg);
              transform: rotate(0) skewX(0deg);
      }
}

@keyframes Poster{
  0%{
      -webkit-transform: rotate(0) translateX(-420%) skewX(50deg);
              transform: rotate(0) translateX(-420%) skewX(50deg);
      -webkit-transform-origin: 50% 50%;
              transform-origin: 50% 50%;
      opacity: 0.75;
      -webkit-box-shadow: 6px 6px 5px 2px black;
              box-shadow: 6px 6px 5px 2px black;
      }
  100%{
      -webkit-transform: rotate(0) skewX(0deg);
              transform: rotate(0) skewX(0deg);
      }
}

@-webkit-keyframes WinningMovie{
  0%{
      -webkit-transform: rotate(xx);
              transform: rotate(xx);
      }
  100%{
      -webkit-transform: rotate(0);
              transform: rotate(0);
      -webkit-transform-origin: 50% 50%;
              transform-origin: 50% 50%;
      -webkit-box-shadow: 10px 10px 5px 2px black;
              box-shadow: 10px 10px 5px 2px black;
      }
}

@keyframes WinningMovie{
  0%{
      -webkit-transform: rotate(xx);
              transform: rotate(xx);
      }
  100%{
      -webkit-transform: rotate(0);
              transform: rotate(0);
      -webkit-transform-origin: 50% 50%;
              transform-origin: 50% 50%;
      -webkit-box-shadow: 10px 10px 5px 2px black;
              box-shadow: 10px 10px 5px 2px black;
      }
}


